/*-----------*/




/*Заголовок и текст*/

.features__info {
	display: flex;
	flex-direction: row;
	align-items: center;
	width: 100%;
	@include bp-992 {
		flex-direction: column;
		align-items: flex-start;
	}

}

.features__text {
	flex: 1 1 55%;
	max-width: 55%;
	margin-right: 4rem;

	@include bp-992 {
		flex-basis: 100%;
		max-width: 100%;
		margin-right: 0;
	}

	h2 {
		margin-bottom: 2rem;
		@include text-anim;
	}
	
	h2._active {
		@include text-anim-show;
	}

	p {
		@include adaptiv-font(20, 16);
		@include text-anim;
	}
	
	p._active {
		@include text-anim-show;
	}
}

.features__mechanisms {
	flex: 1 1 45%;
	max-width: 55%;
	padding: 5rem 7rem;
	border: 2px solid #292547;
	border-radius: 2.5rem;
	@include text-anim;

	@include bp-992 {
		flex-basis: 100%;
		max-width: 100%;
		padding: 3rem 4rem;
	}

	@include bp-480 {
		padding-left: 3rem;
		padding-right: 3rem;
	}

	p {
		@include title-font;
		@include adaptiv-font(24, 20);
		font-weight: 900;
	}
}

.features__mechanisms._active {
	@include text-anim-show;
}

.mechanism__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin: 1.5rem 0;
	.mechanism-ic {
		min-width: 25px;
		height: 25px;
		margin-right: 1.5rem;
		background-image: url('../img/icons/check/check-stroke.svg');
		background-repeat: no-repeat;
		background-size: contain;
	}
	span {
		@include adaptiv-font(16, 14);
	}
}


/*----------*/




/*Карточки*/

.features__cards {
	position: relative;
	height: 100%;
	overflow: hidden;
	opacity: 0;
	transition: all 0.8s ease .3s;
}

.features__cards._active {
	@include text-anim-show;
}

.features-card {
	padding: 6rem 4rem;
	border-radius: 2.5rem;
	cursor: grab;
	background: linear-gradient(#FC5D43, #FB535F);
	background-color: $main-color;


	@include bp-768 {
		padding: 4rem 2.5rem;
	}

	@include bp-480 {
		padding: 2.5rem 2rem;
	}
}




.features-name {
	margin-bottom: 3rem;

	@include bp-768 {
		margin-bottom: 2rem;
	}
}

.features__text {
	p {
		color: $light-text;
		@include adaptiv-font(20, 16);
		line-height: 1.7;
		margin-bottom: 2rem;
	}
}

.features-card__title {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 1.5rem;

	.card-ic {
		min-width: 70px;
		height: 70px;
		background-repeat: no-repeat;
		background-size: contain;
		margin-right: 2rem;
		opacity: 0.6;
		@include bp-768 {
			min-width: 40px;
			height: 40px;
		}
	}

	.feature--one {
		background-image: url('../img/icons/cards/ic-one.svg');
	}
	.feature--two {
		background-image: url('../img/icons/cards/ic-two.svg');
	}
	.feature--three {
		background-image: url('../img/icons/cards/ic-three.svg');
	}
	.feature--four {
		background-image: url('../img/icons/cards/ic-four.svg');
	}
}

.features-card__text {
	p {
		@include adaptiv-font(18, 14);
	}
}

.features-list__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin: 0.8rem 0;
	span {
		margin-right: 1rem;
		display: block;
		min-width: 10px;
		height: 10px;
		border: 1px solid white;
		border-radius: 50%;

		@include bp-992 {
			min-width: 5px;
			height: 5px;
			background-color: #fff;
			opacity: 0.5;
		}

	}
	p {
		@include adaptiv-font(16, 13);
	}
}

/*----------*/


