// ФОРМА ЗАЯВКИ

/*Основа*/

.block6-target {
	position: relative;
	background-image: url('../img/pictures/footer/footer-bg1x.jpg');
	@include bg-x2 ('../img/pictures/footer/footer-bg2x.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;

	@include bp-992 {
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba(21,18,43,0.8);
			z-index: 2;
		  }
	}
}

.final__row {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.final-container,
.footer__container {
	display: block;
	z-index: 3;
}


/*-----------*/




/*Блок*/

.final__row {
	h2 {
		text-align: center;
		margin-bottom: 8rem;
		width: 100%;
		@include text-anim;

		@include bp-992 {
			margin-bottom: 4rem;
		}
	}

	h2._active {
		@include text-anim-show;
	}

	.final__call-btn {
		@include text-anim;
	}
	
	.final__call-btn._active {
		@include text-anim-show;
	}

}



/*----------*/


