.main-btn {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 1.1em 2em 1.1em 2em;
  border-radius: 5em;
  border: transparent;
  overflow: hidden;
  box-shadow: none;
  font-family: Manrope, Roboto, sans-serif;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition-property: background-color, border-color;
  transition-duration: 500ms;
  background-color: #FB535F;
  white-space: nowrap;
  transition: all 0.3s ease;
  outline: none;
  color: #fff;
  background: linear-gradient(#FC5D43, #FB535F);
}
@media (max-width: 767px) {
  .main-btn {
    font-size: calc(18px + 13.6 * ((100vw - 320px) / 1920));
  }
}
@media (min-width: 767px) {
  .main-btn {
    font-size: calc(18px + 8 * (100vw / 1920));
  }
}
.main-btn:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 5rem;
  opacity: 0;
  background: #fff1ef;
  transform: translateX(0);
  transition: all 550ms cubic-bezier(0.5, 1, 0.22, 1);
}
.main-btn span {
  margin-left: 5px;
}
.main-btn span::before {
  font-size: 1.6rem;
}
.main-btn:hover {
  color: #fff;
}
.main-btn:hover:after {
  opacity: 0.2;
}
.main-btn:active:after {
  opacity: 0;
}

