.burger-menu__block {
  overflow-y: auto;
  z-index: 80 !important;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  max-width: 480px;
  width: 100%;
  background-color: #15122B;
  transform: translateX(100%);
  transition: transform 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
