  .burger-menu__block {
    max-width: 100%;
  }
}
.burger-menu__block.show {
  transform: translateX(0px);
}

.close-burger {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 19px 55px;
  border-bottom: 1px solid #201B41;
  cursor: pointer;
  transition: all 0.2s ease;
}
