.contacts .menu-btn__block {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1.5rem;
  height: auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 768px) {
  .contacts .menu-btn__block {
    padding: 1rem;
  }
}
@media only screen and (max-width: 320px) {
  .contacts .menu-btn__block {
    padding: 0.5rem;
  }
}
.contacts .menu-btn {
  position: relative;
  top: 1px;
  width: 3rem;
  height: 2.5rem;
  cursor: pointer;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.contacts .menu-btn span {
  display: block;
  position: absolute;
  height: 0.3rem;
  width: 100%;
  border-radius: 0.8rem;
  background: #fff;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
.contacts .menu-btn span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}
.contacts .menu-btn span:nth-child(2) {
  top: 1rem;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}
.contacts .menu-btn span:nth-child(3) {
  top: 2rem;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}
.contacts .menu-btn.open-burger span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -0.1rem;
  left: 0.4rem;
}
.contacts .menu-btn.open-burger span:nth-child(2) {
  width: 0%;
  opacity: 0;
}
.contacts .menu-btn.open-burger span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 2rem;
  left: 0.3rem;
}

