// ПЕРВЫЙ ЭКРАН

/*Основной блок*/

#first-screen {
	background-image: url('../img/pictures/backgrounds/header-bg-1x.jpg');
	@include bg-x2 ('../img/pictures/backgrounds/header-bg-2x.jpg');
	background-size: cover;
	background-repeat: no-repeat;
}

.banner__container {
	.padding_tb {
		padding-top: 150px;
	}
}

.row__banner {
}

/*-----------*/




/*Заголовок*/

.banner__block {
	flex: 0 1 100rem;
	max-width: 100rem;
	margin-bottom: 6rem;

	.main-title {
		margin-bottom: 5rem;
		@include text-anim;
	}

	.main-title._active {
		@include text-anim-show;
	}

	@include bp-1200 {
		text-align: center;
		flex-basis: 100%;
	}
	@include bp-640 {
		margin-bottom: 5rem;
	}
}


.banner-btn {
	max-width: 41rem;
	@include text-anim;

	@include bp-480 {
		width: 100%;
	}

	@include bp-280 {
		.icon-arrow-tail-bold {
			display: none;
		}
	}
}

.banner-btn._active {
	@include text-anim-show;
}


/*-----------*/




/*Качества*/

.advantages__block {
	display: flex;
	flex-direction: row;
	flex-wrap: no-wrap;
	flex: 0 1 100rem;
	max-width: 100rem;

	@include bp-992 {
		flex-wrap: wrap;
		justify-content: center;
	}

	.advantage {
		display: flex;
		flex-direction: column;
		background-color: rgba(31, 23, 58, 0.8);
		border-radius: 2rem;
		flex: 0 1 25%;
		padding: 2.5rem;
		margin: 1rem;
		@include text-anim;

		&__ic {
			min-width: 70px;
			height: 70px;
			margin-bottom: 1.5rem;
		}

		&__text {
			@include adaptiv-font(18,14);
			font-weight: 600;
		}

		@include bp-992 {
			flex: 0 1 40%;
			max-width: 100%;
		}

		@include bp-640 {
			&__ic {
				min-width: 50px;
				height: 50px;
			}
		}

		@include bp-480 {
			flex: 0 1 100%;
			flex-direction: row;
			align-items: center;
			margin: 0.5rem 0 0.5rem 0;
			&__ic {
				margin-bottom: 0;
				margin-right: 1.5rem
			}
		}

	}	

	.advantage._active {
		@include text-anim-show;
	}

	.adv-anim1 {
		transition: opacity 0.4s ease .2s;
	}
	.adv-anim2 {
		transition: opacity 0.6s ease .3s;
	}
	.adv-anim3 {
		transition: opacity 0.8s ease .4s;
	}
	.adv-anim4{
		transition: opacity 1s ease .5s;
	}
}

.adv__ic--one {
	background-image: url('../img/icons/advantages/ic-one.svg');
	background-repeat: no-repeat;
	background-size: contain;
}

.adv__ic--two {
	background-image: url('../img/icons/advantages/ic-two.svg');
	background-repeat: no-repeat;
	background-size: contain;
}

.adv__ic--three {
	background-image: url('../img/icons/advantages/ic-three.svg');
	background-repeat: no-repeat;
	background-size: contain;
}

.adv__ic--four {
	background-image: url('../img/icons/advantages/ic-four.svg');
	background-repeat: no-repeat;
	background-size: contain;
}



