// ШАПКА


/*Основа*/

.header {
	top: 0;
	left: 0;
	width: 100%;
	z-index: 40;
}

.header__container {
	padding-top: 1rem;
	padding-bottom: 1rem;
}

.header-row {
	flex-direction: row;
	align-items: stretch;
	flex-wrap: nowrap;
	@include bp-480 {
		flex-direction: column;
	}
}

.header--fixed {
	position: fixed;
	background-color: $second-color;
	box-shadow: 0 0 1rem #0f0d1f;

	@include bp-480 {
		.logo-container {
			display: none;
		}
	}


}

/*-----------*/




/*Логотип*/

.logo-container {
	display: flex;
	align-items: center;
	margin-right: 3rem;
	@include bp-480 {
		justify-content: center;
		margin-top: .5rem;
		margin-bottom: 1.5rem;
		margin-right: 0;
		max-width: 100%;
	}
	.logo-company {
		max-width: 18rem;
		a {
			font-size: 2.6rem;
			@include title-font;
			text-transform: uppercase;
			span {
				font-size: 2.6rem;
				@include title-font;
				color: #FD5C48;
				transition-duration: 0.3s;
			}
			&:hover {
				color: white;
				span {
					color: white;
				}
			}
		}


	}
}

/*-----------*/




/*Контакты и кнопка меню*/

.contacts {
	display: flex; 
	flex-direction: row;
	justify-content: flex-end;
	align-items: stretch;
	flex-grow: 2;
	flex-shrink: 0;
	@include bp-480 {
		justify-content: space-between;
	}

	.menu-btn__block {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 1.5rem;
		height: auto;
		box-sizing: border-box;

		@include bp-768 {
			padding: 1rem;
		}
		@include bp-320 {
			padding: 0.5rem;
		}
	}
	
	.menu-btn {
		position: relative;
		top: 1px;
		width: 3rem; 
		height: 2.5rem;
		cursor: pointer;
		position: relative;
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
		-webkit-transition: .5s ease-in-out;
		-moz-transition: .5s ease-in-out;
		-o-transition: .5s ease-in-out;
		transition: .5s ease-in-out;
		 span {
			display: block;
			position: absolute;
			height: 0.3rem;
			width: 100%;
			border-radius: 0.8rem;
			background: $white;
			opacity: 1;
			left: 0;
			-webkit-transform: rotate(0deg);
			-moz-transform: rotate(0deg);
			-o-transform: rotate(0deg);
			transform: rotate(0deg);
			-webkit-transition: .25s ease-in-out;
			-moz-transition: .25s ease-in-out;
			-o-transition: .25s ease-in-out;
			transition: .25s ease-in-out;
		  }
		  
		  span:nth-child(1) {
			top: 0px;
			-webkit-transform-origin: left center;
			-moz-transform-origin: left center;
			-o-transform-origin: left center;
			transform-origin: left center;
		  }
		  
		  span:nth-child(2) {
			top: 1rem;
			-webkit-transform-origin: left center;
			-moz-transform-origin: left center;
			-o-transform-origin: left center;
			transform-origin: left center;
		  }
		  
		  span:nth-child(3) {
			top: 2rem;
			-webkit-transform-origin: left center;
			-moz-transform-origin: left center;
			-o-transform-origin: left center;
			transform-origin: left center;
		  }
		  
		  &.open-burger span:nth-child(1) {
			-webkit-transform: rotate(45deg);
			-moz-transform: rotate(45deg);
			-o-transform: rotate(45deg);
			transform: rotate(45deg);
			top: -0.1rem;
			left: 0.4rem;
		  }
		  
		  &.open-burger span:nth-child(2) {
			width: 0%;
			opacity: 0;
		  }
		  
		  &.open-burger span:nth-child(3) {
			-webkit-transform: rotate(-45deg);
			-moz-transform: rotate(-45deg);
			-o-transform: rotate(-45deg);
			transform: rotate(-45deg);
			top: 2rem;
			left: 0.3rem;
		  }
	}
}


.icon--white {
	color: #fff !important;
}

.text--white {
	color: #fff !important;
}

.mail-header {
	flex: 0 0 23rem;
	@include bp-992 {
		display: none;
	}
}

.phone-header {
	flex: 0 0 22rem;

	@include bp-768 {
		margin-left: 0;
		margin-right: 2rem;
		flex-basis: auto;
		.contacts-ic {
			display: none;
		}
	}
	@include bp-320 {
		margin-right: 0;
	}

}


