// ПОКАЗАТЕЛИ

/*Основа*/

.indicators__row {
	flex-direction: column;
	align-items: center;

	@include bp-640 {
		align-items: flex-start;
	}
}


.paddTB-block5 {
	padding-bottom: 12rem;
	@include bp-992 {
		padding-top: 6rem;
		padding-bottom: 6rem;
	}
	@include bp-768 {
		padding-top: 0;
		padding-bottom: 8rem;
	}
	@include bp-480 {
		padding-bottom: 4rem;
	}
}

/*-----------*/




/*Блок*/

.indicators__title {
	margin-bottom: 4rem;
	text-align: center;
	@include text-anim;

	@include bp-768 {
		margin-bottom: 2rem;
	}

	@include bp-640 {
		text-align: left;

	}

}

.indicators__title._active {
	@include text-anim-show;
}

.indicators__items {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	width: 100%;

	@include bp-992 {
		align-items: flex-start;
	}

	@include bp-768 {
		flex-direction: column;
		padding-left: 70px;
	}

	@include bp-640 {
		padding-left: 0;
	}

	.item {
		display: flex;
		flex-direction: row;
		align-items: center;
		flex: 1 1 33.333%;
		max-width: 33.333%;
		padding: 2rem;
		@include text-anim;

		@include bp-992 {
			align-items: flex-start;
		}

		@include bp-768 {
			padding: 1rem 0;
			flex-basis: 100%;
			max-width: 100%;
			align-items: center;
		}

		@include bp-640 {
			padding-left: 0;
		}

		.ic {
			min-width: 50px;
			height: 50px;
			margin-right: 1.5rem;
			background-image: url('../img/icons/check/check-stroke.svg');
			background-repeat: no-repeat;
			background-size: contain;
			@include bp-992 {
				min-width: 15px;
				height: 15px;
			}
		}

		p {
			@include adaptiv-font(18, 14);
		}
	}

	.item._active {
		@include text-anim-show;
	}

	.ind1 {
		transition: opacity 0.4s ease .2s;
	}
	.ind2 {
		transition: opacity 0.6s ease .3s;
	}
	.ind3 {
		transition: opacity 0.8s ease .4s;
	}

}


/*----------*/


// КОНЕЦ БЛОКА ==========