// НАЗНАЧЕНИЕ

/*Основа*/

.purpose__row {
	flex-direction: row;
	align-items: center;
	@include bp-992 {
		flex-direction: column;
		.purpose-pic {
			order: 2;
		}
	
		.purpose-text {
			order: 1;	
		}
	}



}

.purpose__block {
	flex: 1 1 50%;
	display: flex;
	justify-content: center;
}

/*-----------*/



/*Картинка*/

.purpose__pic {
	position: relative;
	display: flex;
	justify-content: center;
	width: 100%;
	height: auto;
	@include bp-992 {
		margin-top: 3rem;
		width: 70%;
	}
}

.earth-box {
	position: absolute;
	width: 35%;
	left: 50%;
    transform: translate(-50%, 0);
}

.purpose__pic-earth {
	width: 100%;
	height: auto;
	transform:  rotate(45deg);
	transition: all 6s ease;
	opacity: 0;
}

.purpose__pic-earth._active {
	opacity: 1;
	transform-origin: center;
	transform:  rotate(360deg);
}

.purpose__pic-bg {
	max-width: 100%;
	height: auto;
	transition: all 0.8s ease .3s;
	opacity: 0;
}

.purpose__pic-bg._active {
	opacity: 1;
}


/*----------*/




/*Текст*/

.purpose__info {
	h2 {
		margin-bottom: 2.5rem;
		@include text-anim;
	}
	h2._active {
		@include text-anim-show;
	}
	.text {
		@include text-anim;
		p {
			color: $light-text;
			@include adaptiv-font(20, 16);
			line-height: 1.7;
			margin-bottom: 2rem;
		}
		& p:last-child {
			margin-bottom: 0;
		}
	}
	.text._active {
		@include text-anim-show;
	}
}



/*----------*/


