.address, .clock, .mail, .phone {
	display: flex;
	align-items: center;
	margin-left: 1.5rem;
	margin-right: 1.5rem;
	.contacts-ic {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		margin-right: 1rem;
		width: 2rem;
		height: 2rem;
		.icon-map,
		.icon-clock,
		.icon-mail,
		.icon-phone {
			font-size: 1.8rem;
		}
	}
}

.mail {
	.mail-box {
		display: flex;
		flex-direction: column;
		a {
			color: #fff;
			&:hover {
				color: $main-color !important;
			}
		}
		span {
			font-size: 1.4rem;
			color: $light-text;
		}
	}
	p {
		@include adaptiv-font(14,14);
	}
}

.phone {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center;
	max-width: 22rem;
	flex-shrink: 0;
	.contacts-ic {
		margin-right: 5px;
		.icon-phone {
			&:before {
				position: relative;
				top: 1px;
			}
		}
	}
	.phone-box {
		display: flex;
		flex-direction: column;
		.phone-num {
			@include title-font;
			@include adaptiv-font(22,18);
			font-size: 2.2rem;
			&:hover {
				color: $main-color !important;
			}
		}
		.link__call-btn {
			cursor: pointer;
		}
	}

}