// Кнопка


@mixin button($bg: $first, $borderColor: $second) {
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: 1.1em 2em 1.1em 2em;
	border-radius: 5em;
	border: $borderColor;
	overflow: hidden;
	box-shadow: none;
	font-family: Manrope, Roboto, sans-serif;
	@include adaptiv-font(26,18);
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	transition-property: background-color, border-color;
	transition-duration: 500ms;
	background-color: $bg;
	white-space: nowrap;
	transition: all 0.3s ease;
	outline: none;
	color: $white;
	@content;
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		border-radius: 5rem;
		opacity: 0;
		background: #fff1ef;
		transform: translateX(0);
		transition: all 550ms cubic-bezier(0.5, 1, 0.22, 1);
	}

}

.main-btn {
	@include button($btn, transparent);
	background: linear-gradient(#FC5D43, #FB535F);
	span {
		margin-left: 5px;
		&::before {
			font-size: 1.6rem;
		}
	}

	&:hover {
		color: $white;
		&:after {
			opacity: 0.2;
		}
	}

	&:active {
		&:after {
			opacity: 0;
		}
	}
}



