// MAIN INPUT

@mixin input($bg: $first, $borderColor: $second) {
	position: relative;
	display: block;
    width: 100%;
	height: 5rem;
	padding: 1.8rem 2rem 1.8rem 2.8rem;
	border-radius: 5rem;
	border: 1px solid $borderColor;
	font-family: 'Open Sans', Roboto, sans-serif;
	font-size: 1.4rem;
	appearance: none;
	cursor: text;
	outline: none;
	box-shadow: none;
	line-height: normal;
	background-color: $bg;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
	&::placeholder {
		color: #928DB2;
	}
	@content;
	// &:focus {
	// 	// box-shadow: 0 0 0 2px rgba(224, 116, 47, 1);
	// }
}

input[type=text],
input[type=mail],
input[type=tel],
input[type=password] {
	@include input(#0f0d1e, transparent);
	color: #928DB2;
	&:hover {
		border: 1px solid lighten($main-color, 35%);
	}
	&:focus {
		border: 1px solid $btn;
	}
}


.input-box {
	position: relative;
	text-align: right;
	small {
		position: absolute;
		right: 1.5rem;
		top: 1.6rem;
	}
}

.input-container.success input {
	border-color: $success;
}

.input-container.error input {
	border-color: $error;
}

.input-container small {
	color: $error;
	visibility: hidden;
}

.input-container.error small {
	visibility: visible;
}

