// СФЕРЫ ПРИМИНЕНИЯ
 
/*Основа*/

.fieldactivity__row {
	flex-direction: column;
}


/*-----------*/




/*Тексты*/

.fieldactivity__info {
	display: flex;
	flex-direction: row;

	.desc {
		max-width: 82.6rem;
		
		@include bp-1400 {
			margin-bottom: 5rem;
			max-width: 100%;
		}

		&__title {
			margin-bottom: 1.5rem;
			@include text-anim;
		}
		.desc__title._active {
			@include text-anim-show;
		}

		&__text {
			margin-bottom: 4rem;
			@include text-anim;
			p {
				color: $light-text;
				@include adaptiv-font(20, 16);
				line-height: 1.7;
				margin-bottom: 2rem;
			}
		}

		.desc__text._active {
			@include text-anim-show;
		}

		.gerb {
			display: flex;
			flex-direction: row;
			align-items: center;
			@include text-anim;

			@include bp-480 {
				flex-direction: column;
				align-items: flex-start;
			}

			&__img {
				min-width: 60px;
				height: 60px;
				margin-right: 1.5rem;
				background-image: url('../img/icons/gerb.svg');
				background-repeat: no-repeat;
				background-size: contain;

				@include bp-480 {
					marign-right: 0;
					margin-bottom: 1rem;
				}

			}

			&__text {
				p {
					font-size: 1.4rem;
					margin-bottom: 0.5rem;
				}
				span {
					font-size: 1.1rem;
					color: $light-text;
				}
			}
		}

		.gerb._active {
			@include text-anim-show;
		}
	}

	.pic {
		min-width: 430px;
		height: 320px;
		overflow: hidden;
		opacity: 0;
		transition: all 0.8s ease .3s;

		@include bp-1400 {
			display: none;
		}
	}
	
	.pic._active {
		opacity: 1;
	}

}

/*----------*/




/*Список*/


.fieldactivity__list {
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding: 4rem 4rem 2rem 4rem;
	border-radius: 3.5rem; 
	background-color: #1D193B;

	@include bp-992 {
		flex-direction: column;
		padding-bottom: 4rem;
	}

	@include bp-480 {
		padding: 4rem 2rem;
	}

}

.fieldactivity__items {
	flex: 1 1 50%;
	max-width: 50%;
	padding: 3rem;


	@include bp-992 {
		flex-basis: 100%;
		max-width: 100%;
		padding: 0;
	}

}

.fieldactivity__items._active {
	@include text-anim-show;
}

.fieldactivity__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 3rem;

	@include bp-992 {
		margin-bottom: 2rem;
	}

	@include bp-480 {
		margin-bottom: 1.5rem;
	}

	.item-ic {
		min-width: 35px;
		height: 35px;
		margin-right: 1.5rem;
		background-image: url('../img/icons/check/check-circle.svg');
		background-size: contain;
		background-repeat: no-repeat;

		@include bp-992 {
			min-width: 15px;
			height: 15px;
		}

		@include bp-480 {
			margin-right: 1rem;
		}

		@include bp-300 {
			min-width: 8px;
			height: 8px;
		}

	}

	p {
		@include adaptiv-font(16, 13);
		line-height: 1.6;
	}


}

.last-it2 {
	margin-bottom: 0;

}

/*----------*/


