// ПОДВАЛ

/*Основной блок*/

footer {
	width: 100%;
	bottom: 0;
	border-top: 1px solid #443B75;
}

.footer__row {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding-top: 2.5rem;
	padding-bottom: 2.5rem;

	@include bp-768 {
		flex-direction: column;
	}

}


/*-----------*/




/*Лого и описание*/

.footer__logo {
	flex: 1 1 50%;
	max-width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;

	@include bp-992 {
		flex-direction: column;
		align-items: flex-start;
	}

	@include bp-768 {
		flex-basis: 100%;
		order: 2;
		align-items: center;
		text-align: center;
	}

	a {
		font-size: 2.6rem;
		text-transform: uppercase;
		@include title-font;
		margin-right: 2rem;
		color: #5B5086;

		@include bp-992 {
			font-size: 1.6rem;
			margin-bottom: 1rem;
		}

		@include bp-768 {
			order: 2;
			margin-bottom: 0;
			margin-right: 0;
		}

		&:hover {
			color: white;
		}
	}

	.footer__copyright {
		display: flex;
		align-items: center;
		margin-right: 2rem;

		@include bp-768 {
			margin-right: 0;
			margin-bottom: 2rem;
			max-width: 480px;
		}


		p {
			font-size: 1.2rem;
			color: #aea6db;
		}

	}
}

/*-----------*/




/*Контакты*/

.footer__contacts {
	flex: 1 1 50%;
	max-width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;

	@include bp-768 {
		flex-basis: 100%;
		justify-content: space-between;
		margin-bottom: 3rem;
		order: 1;
	}

	@include bp-640 {
		flex-direction: column;
	}

	.footer__links {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		margin-right: 2rem;

		@include bp-640 {
			justify-content: center;
			text-align: center;
			order: 2;
			margin-right: 0;
		}

		& div:last-child {
			margin-bottom: 0;
		}

		.link {
			line-height: 1;
			margin-bottom: 1rem;
			a {
				font-size: 1.2rem;
				color: $white;
				text-align: left !important;
				&:hover {
					color: $main-color !important;
				}
			}
		}
	}

	.mail-phone {

		@include bp-640 {
			order: 1;
			margin-bottom: 3rem;
		}

		.mail {
			margin-bottom: 1.2rem;
			a {
				color: $white;
				&:hover {
					color: $main-color !important;
				}
			}
		}
		.phone {
			a {
				@include title-font;
				@include adaptiv-font(22,18);
				font-size: 2.2rem;
				white-space: nowrap;
				&:hover {
					color: $main-color !important;
				}
			}
		}

	}

}

/*-----------*/


